import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    currentSearchKeyword: [],
    changesex: "girl",
    bookid:null,
    bookslist:[],
    bookname:"",
    bookcontent:[],
    bookcontents:{}
  },
  mutations: {
    setCurrentSearchKeyword(state, param) {
      state.currentSearchKeyword.push(param.value)
    },
    changesexnow(state, param) {
      state.changesex = param.sex;
    },
    changebookid(state,id){
      state.bookid = id.id;
    },
    changebookslist(state,list){
      state.bookslist = list.list;
    },
    changebookname(state,name){
      state.bookname = name.name;
    },
    getbookcontent(state,content){
      state.bookcontent = content.content;
      state.bookcontents = content.contents;
    },
  },
  actions: {},
  modules: {}
})