<template>
  <div class="scrollbar">
    <div class="book" ref="lyricl" @touchmove="movetouch">
      <van-popup :duration="0.2" v-model="show" position="top">
        <van-nav-bar left-text="返回" left-arrow @click-left="gotobook">
          <div class="title" slot="title">{{this.$store.state.bookname}}</div>
          <div class="bookuser" slot="right">
            <van-icon name="wap-nav" size="0.6rem" />
          </div>
        </van-nav-bar>
      </van-popup>
      <van-popup :duration="0.2" v-model="show" position="bottom" :overlay="false">
        <div class="butts">
          <button class="buttonp" @click="changepage(inx-1)">上一章</button>
          <button class="buttonc" @click="showbooklist">目录</button>
          <button class="buttonn" @click="changepage(inx+1)">下一章</button>
        </div>
      </van-popup>

      <div class="boook" @click="showPopup">
        <h1 class="bookh">{{title}}</h1>
        <p class="bookp" v-for="(item, index) in xxxx" :key="index">{{item}}</p>
        <div class="booook" ref="booook"></div>
      </div>
      <div v-if="bookshow" @click="bookshow = false" class="lajichang">
        <van-overlay :show="bookshow" />
        <BookList :list="$store.state.bookslist"></BookList>
      </div>
    </div>
  </div>
</template>

<script>
// var oDiv = document.querySelector(".lajichang");
// oDiv.onmousedown = function(e) {
//   e.stopPropagation();
//   e.cancleBubble = true;
// };
import BookList from "@/components/BookList.vue";
export default {
  components: {
    BookList
  },
  data() {
    return {
      // conents: {},
      links: "",
      xxxx: [],
      show: false,
      bookshow: false,
      fff: true, //只执行一次的判定
      oncesuccess: null,
      nowtitle: "",
      inx: null,
      title: "",
      tt: ""
    };
  },
  watch: {
    oncesuccess: function() {
      this.fff = true;
    },
    $route: function() {
      document.querySelector(".booook").innerHTML = "";
      this.$refs.lyricl.scrollTop = 0;
      this.xxxx = this.$store.state.bookcontent;
      this.title = this.$store.state.bookcontents.title;
      this.inx = this.$route.query.inx;
    }
  },
  created() {
    this.xxxx = this.$store.state.bookcontent;
    this.title = this.$store.state.bookcontents.title;
    this.inx = this.$route.query.inx;
  },
  methods: {
    gotobook() {
      this.$router.go(-1);
      // var patt1 = new RegExp(/book\?inx=/);
      // if (patt1.test(window.location.href)) {
      //   window.console.log("^_^")
      //   this.gotobook();
      // }
    },
    showbooklist() {
      this.bookshow = true;
      this.show = false;
    },
    showPopup() {
      this.show = true;
    },
    movetouch() {
      this.oncesuccess = this.$refs.lyricl.scrollHeight;
      if (
        this.$refs.lyricl.scrollTop >= this.$refs.lyricl.scrollHeight - 641 &&
        this.fff
      ) {
        this.fff = false;

        this.inx = this.inx + 1;
        this.links = encodeURIComponent(
          this.$store.state.bookslist[this.inx].link
        );

        this.axios
          .get("https://novel.kele8.cn/chapters/" + this.links)
          .then(response => {
            this.nowtitle = response.data.chapter.title;
            var tag = "<h1 class='bookh'>" + this.nowtitle + "</h1>";
            this.$refs.booook.insertAdjacentHTML("beforeEnd", tag);
            this.$store.commit("getbookcontent", {
              content: response.data.chapter.cpContent.split(/\n/),
              contents: response.data.chapter
            });

            for (
              let index = 0;
              index < this.$store.state.bookcontent.length;
              index++
            ) {
              this.$refs.booook.insertAdjacentHTML(
                "beforeEnd",
                "<p class='bookp'>" +
                  this.$store.state.bookcontent[index] +
                  "</p>"
              );
            }
          });
      }
    },
    changepage(x) {
      if (x != this.$route.query.inx) {
        this.links = encodeURIComponent(this.$store.state.bookslist[x].link);
        this.axios
          .get("https://novel.kele8.cn/chapters/" + this.links)
          .then(response => {
            this.$store.commit("getbookcontent", {
              content: response.data.chapter.cpContent.split(/\n/),
              contents: response.data.chapter
            });
            this.$router.replace({
              path: "/book",
              query: { inx: x }
            });
          });
      } else {
        this.$refs.lyricl.scrollTop = 0;
        this.inx = x;
      }
    }
  }
};
</script>

<style lang="less">
.scrollbar {
  width: 100%;
  height: 100%;
  overflow: hidden;
  .book {
    background-color: #eee6dd;
    width: calc(100% + 17px);
    height: 100%;
    // height: calc(100% + 17px);
    overflow: auto;
    .van-overlay {
      background: rgba(0, 0, 0, 0);
    }
    .boook {
      padding: 15px 32px 0 15px;
      .bookh {
        font-size: 22px;
        font-weight: 500;
        line-height: 30px;
      }
      .bookp {
        margin: 0;
        color: #444;
        font-size: 18px;
        line-height: 36px;
        white-space: pre-line;
        text-indent: 2em;
      }
    }
    .van-popup {
      background-color: rgba(0, 0, 0, 0.8);
      .van-nav-bar {
        background-color: rgba(0, 0, 0, 0);
        .van-nav-bar__left {
          .van-icon {
            color: #fff;
            font-size: 25px;
          }
          .van-nav-bar__text {
            padding-left: 20px;
            color: #fff;
          }
        }
        .title {
          color: #fff;
        }
        i {
          color: #fff;
        }
      }
      .butts {
        display: flex;
        font-size: 0.4rem;
        margin: 20px 0;
        color: #fff;
        background: none;
        button {
          flex: 1;
          background: none;
          border: none;
          outline: none;
        }
        .buttonp {
          margin: 0 10px;
        }
        .buttonc {
          margin: 0 30px;
        }
        .buttonn {
          margin: 0 10px;
        }
      }
    }
    .lajichang {
      width: 100%;
      height: 100%;
      position: absolute;
      top: 0;
      left: 0;
    }
    .booklist {
      position: absolute;
      z-index: 10;
      width: 80%;
      height: 100%;
      background: #fff;
      overflow: scroll;
      .listbook {
        .lists {
          line-height: 1.33rem;
          height: 1.33rem;
          border-bottom: 1px solid #f8f8f8;
          margin-left: 0.53333rem;
          padding-right: 0.53333rem;
          font-size: 12px;
          color: #999;
          .itemtitle {
            float: left;
          }
          .notread {
            float: right;
            height: 1.33rem;
            width: 0.46667rem;
            background-image: url("data:image/png;base64,");
            background-size: 0.46667rem;
            background-position: center;
            background-repeat: no-repeat;
          }
          &:after {
            content: "";
            clear: both;
            display: block;
            font-size: "0";
            height: 0;
          }
        }
      }
    }
  }
}
</style>