<template>
  <div class="searching">
    <BookTitle :titlename="titlename"></BookTitle>
    <van-search v-model="value" placeholder="请输入搜索关键词" show-action shape="round" @search="getSearchKeyword(value)">
      <div slot="action" @click="getSearchKeyword(value)">搜索</div>
    </van-search>
    <div v-if="value">
      <ul>
        <li class="searchwordli" v-for="(item, index) in searchword" :key="index" @click="getSearchKeyword(item)">{{item}}</li>
      </ul>
    </div>
    <div v-else class="hot">
      <h1 class="hothot">热门推荐</h1>
      <ul class="ul">
        <li class="hotword" v-for="(item, index) in hotword" :key="index" @click="getSearchKeyword(item)">{{item}}</li>
      </ul>
      <h1 class="hothot">搜索历史</h1>
      <ul class="ul">
        <li class="hotword" v-for="(item, index) in $store.state.currentSearchKeyword" :key="index" @click="getSearchKeyword(item)">{{item}}</li>
      </ul>
    </div>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
export default {
  components: {
    BookTitle
  },
  data() {
    return {
      titlename: "搜索",
      hotword: [],
      value: "",
      searchword:[]
    };
  },
  watch: {
    value:function(){
      this.axios.get("https://novel.kele8.cn/auto-complete?query="+this.value).then(repson => {
        this.searchword = repson.data.keywords;
      });
    }
  },
  created() {
    this.gethotsearch();
  },
  methods: {
    gethotsearch() {
      this.axios.get("https://novel.kele8.cn/hot-books").then(repson => {
        this.hotword = repson.data.hotWords;
      });
    },
    getSearchKeyword(name) {
      this.$store.commit("setCurrentSearchKeyword", {
        value: name
      });
      this.$router.push({
        path: "/search",
        query: { keyword: name }
      });
    }
  }
};
</script>

