<template>
  <div class="homenav">
    <van-nav-bar>
      <van-image
        width="2.08rem"
        height="0.48rem"
        fit="cover"
        src="data:image/png;base64,"
        slot="left"
      />
      <div class="button-group" slot="title">
        <button :class="{active:this.$store.state.changesex=='boy'}" @click="nowchangesex">男生</button>
        <button :class="{active:this.$store.state.changesex=='girl'}" @click="nowchangesex">女生</button>
      </div>
      <div class="bookuser" slot="right">
        <van-icon name="bookmark" size="0.6rem" />
        <van-icon name="manager" size="0.6rem" />
      </div>
    </van-nav-bar>
  </div>
</template>

<script>
export default {
  methods: {
    nowchangesex() {
      if (this.$store.state.changesex == "girl") {
        this.$store.commit("changesexnow", {
          sex: "boy"
        });
      } else {
        this.$store.commit("changesexnow", {
          sex: "girl"
        });
      }
    }
  }
};
</script>

<style lang="less">
.homenav {
  .van-nav-bar {
    background: #b83320;
    height: 1.3rem;
    .van-image {
      top: 0.1rem;
    }
    .button-group {
      height: 1.3rem;
      button {
        height: 0.5rem;
        line-height: 0rem;
        padding: 13px 8px;
        font-size: 14px;
        outline: none;
        border: 1px solid #fff;
        background: rgba(255, 255, 255, 0);
        color: #fff;
        &:first-child {
          border-radius: 16px 0 0 16px;
        }
        &:last-child {
          border-radius: 0 16px 16px 0;
        }
      }
      .active {
        background: #fff;
        color: rgb(150, 150, 150);
      }
    }
    .bookuser {
      .van-icon {
        color: #fff;
        margin-left: 10px;
      }
    }
  }
}
</style>