<template>
  <div class="booklist">
    <ul class="listbook">
      <li class="lists" v-for="(item, index) in list" :key="index" @click="enterinto(index)">
        <div class="itemtitle">{{item.title}}</div>
        <div v-if="item.isVip" class="notread"></div>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  props: ["list"],
  data() {
    return {
      links: ""
    };
  },
  beforeRouteEnter(to, from, next) {
    next(vm=>{window.console.log(vm)})
  },
  methods: {
    enterinto(x) {
      this.links = encodeURIComponent(this.$store.state.bookslist[x].link);

      this.axios
        .get("https://novel.kele8.cn/chapters/" + this.links)
        .then(response => {
          this.$store.commit("getbookcontent", {
            content: response.data.chapter.cpContent.split(/\n/),
            contents: response.data.chapter
          });
          this.$router.replace({
            path: "/book",
            query: { inx: x }
          });
        });
    }
  }
};
</script>

<style lang="less">
.booklists {
  .listbook {
    .lists {
      line-height: 1.33rem;
      height: 1.33rem;
      border-bottom: 1px solid #f8f8f8;
      margin-left: 0.53333rem;
      padding-right: 0.53333rem;
      font-size: 12px;
      color: #999;
      .itemtitle {
        float: left;
      }
      .notread {
        float: right;
        height: 1.33rem;
        width: 0.46667rem;
        background-image: url("data:image/png;base64,");
        background-size: 0.46667rem;
        background-position: center;
        background-repeat: no-repeat;
      }
      &:after {
        content: "";
        clear: both;
        display: block;
        font-size: "0";
        height: 0;
      }
    }
  }
}
</style>