<template>
  <div class="booklists">
    <BookTitle :titlename="titlename"></BookTitle>
    <div class="lis">
      <div class="liss">目录</div>
      <div class="lisss">共{{howcount}}章</div>
      <div class="fall" @click="changefall">
        <div v-if="xxx">倒序</div>
        <div v-else>正序</div>
      </div>
    </div>
    <BookList :list="list"></BookList>
  </div>
</template>

<script>
import BookTitle from "@/components/BookTitle.vue";
import BookList from "@/components/BookList.vue";
export default {
  components: {
    BookTitle,
    BookList
  },
  data() {
    return {
      titlename: "优质书源",
      list: [],
      howcount: null,
      xxx: true
    };
  },
  methods: {
    changefall() {
      if (this.list) {
        this.list = this.list.reverse();
        this.xxx = !this.xxx;
      }
    },
    
  },
  created() {
    this.howcount=this.$route.query.howcounts;
    this.list = this.$store.state.bookslist
  },
};
</script>

<style lang="less">
.booklists {
  .lis {
    line-height: 1.33rem;
    height: 1.33rem;
    border-bottom: 1px solid #f8f8f8;
    padding: 0 0.53333rem;
    .liss {
      font-size: 14px;
      font-weight: 600;
      float: left;
    }
    .lisss {
      font-size: 12px;
      color: #999;
      float: left;
      margin-left: 0.15rem;
    }
    .fall {
      font-size: 14px;
      float: right;
    }
    &:after {
      content: "";
      clear: both;
      font-size: 0;
      height: 0;
      display: block;
    }
  }
}
</style>