<template>
  <div class="searchlist">
    <BookTitle
      :titlename="'关于\''+$store.state.currentSearchKeyword[$store.state.currentSearchKeyword.length-1]+'\'的书籍'"
    ></BookTitle>
    <ul>
      <NewSearchList v-for="(item, index) in books" :key="index" :item="item"></NewSearchList>
    </ul>
    <div class="nothave">没有更多了</div>
  </div>
</template>

<script>
import Vue from "vue";
import BookTitle from "@/components/BookTitle.vue";
import NewSearchList from "@/components/NewSearchList.vue";
export default {
  components: {
    BookTitle,
    NewSearchList
  },
  beforeRouteEnter(to, from, next) {
    Vue.axios
      .get("https://novel.kele8.cn/search", {
        params: {
          keyword: to.query.keyword
        }
      })
      .then(function(response) {
        next(vm => {
          vm.books = response.data.books;
          // window.console.log(response);
        });
      });
  },
  data() {
    return {
      books: []
    };
  }
};
</script>

<style lang="less">
.searchlist {
  background-color: #f5f2ed;
  height: 100%;
  width: 100%;
  overflow: scroll;
  .nothave {
    height: 1.06667rem;
    line-height: 1.06667rem;
    text-align: center;
    color: #999;
    font-size: 12px;
  }
}
</style>