<template>
  <div class="bookdetails">
    <BookTitle :titlename="titlename"></BookTitle>
    <div class="imgtitle">
      <img :src="changeurl(books.cover)" alt />
      <div class="titlecontent">
        <div class="boottitle">{{books.title}}</div>
        <div class="boo">
          <div class="author">{{books.author}}</div>
          <div class="gang">|</div>
          <div class="minorCate">{{books.minorCate}}</div>
          <div class="gang">|</div>
          <div class="wordCount">{{books.wordCount}}</div>
        </div>
        <div class="isSerial">
          <div v-if="!books.isSerial">已完结</div>
          <div v-else>连载中</div>
        </div>
      </div>
    </div>
    <div class="butotns">
      <button>加入书架</button>
      <button @click="starread(0)">开始阅读</button>
    </div>
    <div class="message">
      <div class="mm">
        <div class="tt">追人气</div>
        <div class="tts">{{books.latelyFollower}}</div>
      </div>
      <div class="mm">
        <div class="tt">读者留存率</div>
        <div class="tts">{{books.retentionRatio}}%</div>
      </div>
      <div class="mm">
        <div class="tt">日更新字数/天</div>
        <div class="tts">{{books.serializeWordCount}}</div>
      </div>
    </div>
    <span :class="{longintro:true,active:shows}">{{books.longIntro}}</span>
    <div :class="{button:true,xxx:!shows}" @click="shows=!shows"></div>
    <div class="catalogue" @click="booklistss">
      <div class="cata">目录</div>
      <div class="catass"></div>
      <div class="catal">{{books.lastChapter}}</div>
      <div class="catas" v-if="!books.isSerial">[完结]</div>
      <div class="catas" v-else>[连载中]</div>
    </div>
  </div>
</template>

<script>
import Vue from "vue";
import BookTitle from "@/components/BookTitle.vue";
export default {
  components: {
    BookTitle
  },
  created() {},
  beforeRouteEnter(to, from, next) {
    Vue.axios
      .get("https://novel.kele8.cn/book-info/" + to.query.bookid)
      .then(function(response) {
        next(vm => {
          vm.books = response.data;
          vm.$store.commit("changebookname", {
            name: response.data.title
          });
          vm.axios
            .get(
              "https://novel.kele8.cn/book-sources?view=summary&book=" +
                vm.$store.state.bookid
            )
            .then(function(response) {
              vm.id = response.data[0]._id;
              vm.howcount = response.data[0].chaptersCount;
            });
        });
      });
  },
  data() {
    return {
      books: {},
      titlename: "书籍详情",
      shows: true,
      links: "",
      howcount: "",
      id: null
    };
  },
  methods: {
    starread(x) {
      this.axios
        .get("https://novel.kele8.cn/book-chapters/" + this.id)
        .then(repson => {
          this.$store.commit("changebookslist", {
            list: repson.data.chapters
          });
          this.links = encodeURIComponent(this.$store.state.bookslist[x].link);
          this.axios
            .get("https://novel.kele8.cn/chapters/" + this.links)
            .then(response => {
              this.$store.commit("getbookcontent", {
                content: response.data.chapter.cpContent.split(/\n/),
                contents: response.data.chapter
              });
              this.$router.replace({
                path: "/book",
                query: { inx: x }
              });
            });
        });
    },

    changeurl(url) {
      if (url) {
        return unescape(url.slice(7));
      }
    },
    booklistss() {
      this.axios
        .get("https://novel.kele8.cn/book-chapters/" + this.id)
        .then(repson => {
          this.$store.commit("changebookslist", {
            list: repson.data.chapters
          });
          this.$router.push({
            path: "/booklists",
            query: { howcounts: this.howcount }
          });
        });
    }
  }
};
</script>

<style lang="less">
.bookdetails {
  .imgtitle {
    padding: 0.5rem;
    img {
      height: 2.33rem;
      width: 1.73rem;
      float: left;
    }
    .titlecontent {
      float: left;
      padding-left: 0.3rem;
      .boottitle {
        font-size: 0.44rem;
        line-height: 0.44rem;
        height: 0.7rem;
      }
      .boo {
        font-size: 0.33rem;
        line-height: 0.33rem;
        height: 0.6rem;
        .gang {
          padding: 0 10px;
          float: left;
          color: #777;
        }
        .author {
          float: left;
          color: #b93321;
        }
        .minorCate {
          float: left;
          color: #777;
        }
        .wordCount {
          float: left;
          color: #777;
        }
        &:after {
          content: "";
          clear: both;
          display: block;
          font-size: "0";
          height: 0;
        }
      }
      .isSerial {
        color: #777;
        font-size: 0.33rem;
      }
    }
    &:after {
      content: "";
      clear: both;
      display: block;
      font-size: "0";
      height: 0;
    }
  }
  .butotns {
    display: flex;
    justify-content: center;
    button {
      line-height: 1.1rem;
      width: 4rem;
      font-size: 17px;
      border: #b93321 1px solid;
      border-radius: 3px;
      &:first-child {
        background: #fff;
        margin-right: 30px;
        color: #b93321;
      }
      &:last-child {
        background: #b93321;
        color: #fff;
      }
    }
  }
  .message {
    margin: 0.5rem 0;
    padding: 0.5rem 0;
    display: flex;
    text-align: center;
    border-top: 1px solid #eee;
    border-bottom: 1px solid #eee;
    .mm {
      flex: 1;
      .tt {
        font-size: 12px;
        color: #999;
      }
      .tts {
        font-size: 16px;
      }
    }
  }
  .longintro {
    font-size: 0.35rem;
    padding: 0 0.5rem;
    line-height: 0.67rem;
    white-space: pre-line;
    display: -webkit-box;
  }
  .active {
    -webkit-line-clamp: 3;
    -webkit-box-orient: vertical;
    overflow: hidden;
  }
  .button {
    float: right;
    width: 15px;
