<template>
  <div class="recommendlist">
      <li class="recommendli" @click="passbookdetails(item._id)">
        <img :src="srccover(item.cover)" alt />
        <div class="recomtitle">
          <h3>{{item.title}}</h3>
          <p>{{item.shortIntro}}</p>
          <div class="recomminorcates">
            <div class="hhh">{{item.cat}}</div>
            <div class="hhhh">|</div>
            <div class="latelyFollower">{{(item.latelyFollower/10000).toFixed(1)}}万</div>
            <div class="renqi">人气</div>
            <div class="majorcates">{{item.cat}}</div>
          </div>
        </div>
      </li>
  </div>
</template>

<script>
export default {
  props: ["item"],
  methods: {
    passbookdetails(ids) {
      this.$store.commit("changebookid", {
        id: ids
      });
      this.$router.push({
        path: "/bookdetails",
        query: { bookid: ids }
      });
    },
    srccover(url) {
      return unescape(url.slice(7));
    }
  },
  data() {
    return {
      rankbooks: []
    };
  }
};
</script>

<style lang="less">
.recommendlist {
  .recommendli {
    display: flex;
    padding: 0.3rem 0.4rem;
    img {
      height: 2.13333rem;
    }
    .recomtitle {
      margin-left: 0.4rem;
      h3 {
        font-size: 14px;
        line-height: 0.61333rem;
        font-weight: 400;
        margin: 0;
      }
      p {
        font-size: 12px;
        margin: 0.06667rem 0 0.13333rem;
        line-height: 0.45333rem;
        white-space: pre-line;
        display: -webkit-box;
        -webkit-line-clamp: 2;
        -webkit-box-orient: vertical;
        overflow: hidden;
        color: #999;
      }
      .recomminorcates {
        display: flex;
        .hhh {
          color: #666;
          font-size: 12px;
        }
        .hhhh {
          color: #999;
          font-size: 12px;
          margin: 0 7px;
          font-weight: 600;
        }
        .latelyFollower {
          color: #b93321;
          font-size: 12px;
        }
        .renqi {
          color: #666;
          font-size: 12px;
          flex: 1;
        }
        .majorcates {
          transform: scale(0.8);
          padding: 0 0.10667rem;
          height: 0.42667rem;
          line-height: 0.4rem;
          border: 1px solid #ff8400;
          color: #ff8400;
          font-size: 12px;
          border-radius: 2px;
        }
      }
    }
  }
}
</style>