<template>
  <div class="homesearch">
    <van-search left-icon :clearable="false">
      <van-icon name="search" slot="right-icon" @click="getCurrentSearchKeyword" />
    </van-search>
    <van-swipe
      vertical
      :show-indicators="false"
      :autoplay="3000"
      :duration="1000"
      :touchable="false"
      @change="getCurrentSearchIndex"
    >
      <van-swipe-item
        v-for="(item, index) in hotword.slice(0, 5)"
        :key="index"
        @click="$router.push('/search/searching')"
      >{{item}}</van-swipe-item>
    </van-swipe>
  </div>
</template>

<script>
export default {
  data() {
    return {
      hotword: [],
      indexs: 0,
      value: "",
    };
  },
  mounted() {
    this.gethotsearch();
  },
  methods: {
    xxx() {
      window.console.log("qweqweqwe");
    },
    gethotsearch() {
      this.axios.get("https://novel.kele8.cn/hot-books").then(repson => {
        this.hotword = repson.data.hotWords;
      });
    },
    getCurrentSearchIndex(index) {
      this.indexs = index;
    },
    getCurrentSearchKeyword() {
      this.$store.commit("setCurrentSearchKeyword", {
        value: this.hotword[this.indexs]
      });
      this.$router.push({
        path: "/search",
        query: { keyword: this.hotword[this.indexs] }
      });
    }
  }
};
</script>

<style lang="less">
.homesearch {
  height: 1.2rem;
  .van-search {
    padding: 0.15rem 0.2rem;
    .van-search__content {
      border-radius: 5px;
      background-color: #e9e9e9;
      height: 0.9rem;
      .van-cell {
        padding: 0.1rem;
      }
      .van-field__body {
        height: 0.7rem;
      }
    }
    .van-icon {
      font-size: 21px;
      font-weight: 600;
      color: rgb(146, 146, 146);
      padding-right: 7px;
    }
  }
  .van-swipe {
    z-index: 6;
    height: 0.9rem;
    line-height: 0.9rem;
    width: 80%;
    top: -1.05rem;
    left: 0.2rem;
    font-size: 0.4rem;
    font-weight: 400;
    color: #999;
    padding-left: 0.5rem;
  }
}
</style>